:root {
  --main-font: "Manrope";
  --secondary-font: "Poppins";
  --weather-font: "Roboto";
  --background-color-light: #f4f4f4;
  --text-color: #111321;
  --secondary-text-color: #4440f7;
  --additional-text-color: #a8a8a8;
  --color-btn-white: #fff;
  --color-active-btn: #4440f6;
  --color-fon-footer: #7a7a7a;
  --headerborder: rgba(0, 0, 0, .2);
  --markercolor: #4b48db;
  --blue--translucent: rgba(68, 64, 247, .7);
  --text-color-dark-theme: #f4f4f4;
  --background-color-dark: #2e2e2e;
  --dark-text: #5f6775;
  --green-text: #00dd73;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  max-width: 100%;
  display: block;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

body.dark {
  background-color: var(--background-color-dark);
  color: var(--text-color-dark-theme);
}

#preloader {
  width: 100%;
  height: 100%;
  background: var(--color-btn-white);
  z-index: 9999;
  opacity: 1;
  justify-content: center;
  align-items: center;
  transition: opacity .3s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader {
  border: 5px solid var(--additional-text-color);
  border-top: 5px solid var(--color-active-btn);
  width: 100px;
  height: 100px;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hide #preloader {
  opacity: 0;
  pointer-events: none;
}

.preloader {
  width: 100vw;
  height: 90%;
  z-index: 1001;
  color: var(--color-active-btn);
  opacity: 1;
  transition: all .1s ease-in-out;
  position: absolute;
  top: 176px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.preloader__image {
  width: 70px;
  height: 70px;
  text-align: center;
  margin-top: -35px;
  margin-left: -35px;
  animation: preloader-rotate 2s linear infinite;
  position: relative;
  top: 35%;
  left: 50%;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

.preloader.loaded {
  opacity: 0;
  pointer-events: none;
}

.header {
  min-height: 81px;
  border-bottom: 1px solid var(--headerborder);
  z-index: 1000;
}

.header.fixed-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

