.ball {
  cursor: url("touch.d5c2050c.svg"), pointer;
  background-color: var(--color-btn-white);
  border: 1px solid var(--color-active-btn);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.ball:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: var(--color-active-btn);
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

input:checked + .ball:before {
  background-color: var(--color-btn-white);
}

input:checked + .ball {
  background-color: var(--color-active-btn);
  border: 1px solid var(--color-btn-white);
}

input:checked + .ball:before {
  transform: translateX(20px);
}

.ball.round {
  border-radius: 34px;
}

.ball.round:before {
  border-radius: 50%;
}

.choseTheme__dark, .choseTheme__light {
  display: none;
}

@media screen and (min-width: 1280px) {
